import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";

import { Container, Nav, Navbar, Row, Col } from "react-bootstrap";

import UseJquery from "./UseJquery";
import { useState } from "react";
import data from "./data.js";

import { Routes, Route, Link } from "react-router-dom";
import Company from "./routes/Company";
import Product from "./routes/Product";

function App() {
  let [goods] = useState(data);
  return (
    <div className="App">
      <UseJquery />
      <Navbar expand="lg" collapseOnSelect bg="dark" variant="dark">
        <Container>
          <Navbar.Brand as={Link} to="/">
            <img src="https://jellyzoo.github.io/ex_img/logo.png" alt="logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />

          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/Company">
                
              </Nav.Link>
              <Nav.Link as={Link} to="/Product">
                
              </Nav.Link>
              <Nav.Link as={Link} to="/inpo">
                
              </Nav.Link>
              <Nav.Link as={Link} to="/notice">
                
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Routes>
        <Route path="/" />
        <Route path="/Company" element={<Company />} />
        <Route path="/Product" element={<Product />} />
      </Routes>

      {/*    */}
      <div
        className="main-gb"
        style={{
          backgroundImage: process.env.PUBLIC_URL + "/img/pc_main_02.jpg",
          height: "600px",
          backgroundPosition: "center",
        }}
      ></div>
      {/*   */}
      <Container>
        {/* <Card goods={goods[0]} i={1} /> */}
        <Row>
          {goods.map((a, i) => {
            return <Card goods={goods[i]} i={i} />;
          })}
        </Row>
      </Container>
    </div>
  );
}

// 
function Card(props) {
  return (
    <Col>
      <img
        src={
          process.env.PUBLIC_URL + "/img/cont3_cake" + (props.i + 1) + ".png"
        }
        width="50%"
        alt="cake"
      />
      <h4>{props.goods.title}</h4>
      <p>{props.goods.content}</p>
    </Col>
  );
}

export default App;
